const axios = require("axios");

export async function getSingleWeather(location) {
  const { name } = location;
  console.log("getSingleWeather -> name", name);
  try {
    const result = await axios({
      method: "GET",
      url: "https://api.openweathermap.org/data/2.5/weather?",
      headers: {
        "content-type": "application/json",
      },
      params: {
        q: `${name}`,
        APPID: process.env.WEATHER_API_KEY,
      },
    });
    return result;
  } catch (error) {
    console.log(error);
    return {};
  }
}

export async function getWeather(IDs) {
  const listIDs = IDs.join(",");

  try {
    const result = await axios({
      method: "GET",
      url: "https://api.openweathermap.org/data/2.5/group?",
      headers: {
        "content-type": "application/json",
      },
      params: {
        id: `${listIDs}`,
        APPID: process.env.WEATHER_API_KEY,
        units: "metric",
      },
    });
    return result;
  } catch (error) {
    console.log(error);
    return {};
  }
}

export async function getPhotos(query) {
  return await axios({
    method: "GET",
    url: "https://api.unsplash.com/search/photos?",
    headers: {
      "content-type": "application/json",
    },
    params: {
      client_id: process.env.PHOTOS_API_KEY,
      query: query,
    },
  });
}
