import React from "react";

const cardStyle = {
  borderRadius: "8px",
  minHeight: "350px",
  minWidth: "350px"
};

const CURRENT_TEMPERATURE = "current_temp";
const MAX_TEMPERATURE = "max_temp";
const MIN_TEMPERATURE = "min_temp";

function Card(props) {
  const formatTemperature = temperature => {
    const temperatureProps = props.weather.main;
    if (!temperatureProps) {
      return "";
    }

    switch (temperature) {
      case CURRENT_TEMPERATURE:
        return `${Math.round(temperatureProps.temp)}°`;
      case MAX_TEMPERATURE:
        return `${Math.round(temperatureProps.temp_max)}°`;
      case MIN_TEMPERATURE:
        return `${Math.round(temperatureProps.temp_min)}°`;
      default:
        return "";
    }
  };

  return (
    <div className={`max-w-sm rounded overflow-hidden shadow-lg bg-white`} style={cardStyle}>
      <img className="h-56 mb-0 w-full object-cover" src={`${props.image ? props.image : ""}`} alt="" />
      <div className="px-4 py-4">
        <div className="font-bold text-xl mb-2">{props.title}</div>
        {props.weather && (
          <section className="text-right">
            <p className="font-sans text-gray-700 text-2xl text-left">{`${formatTemperature(CURRENT_TEMPERATURE)}`}</p>
            <p className="text-gray-700 text-base">{`${formatTemperature(MAX_TEMPERATURE)}`}</p>
            <p className="text-gray-700 text-base">{`${formatTemperature(MIN_TEMPERATURE)}`} </p>
          </section>
        )}
      </div>
    </div>
  );
}

Card.defaultProps = {
  temperature: undefined,
  image: undefined,
  title: ""
};

export default Card;
